.loading-animation {
  left: 0;
  position: fixed;
  top: 0;
  background-color: #fff;
  height: 100vh; /* 縦いっぱい */
  width: 100vw; /* 横幅いっぱい */
  z-index: 9996;
  height: calc(var(--vh) * 100);
  display: none;
}

.loading-animation.is-active {
  display: block;
}

.loading-animation-main.is-active {
  opacity: 0;
  visibility: hidden;
}

.loading-animation__image {
  position: relative;
  width: 100%;
  height: 100vh;
  display: flex;
  z-index: 9998;
}

.loading-animation__left,
.loading-animation__right {
  display: inline-block;
  width: 50%;
  transition: opacity 0.3s ease-out;
  opacity: 0;
}

.loading-animation__left {
  animation-name: fadeUpAnimeLeft;
  animation-duration: 4s;
  animation-fill-mode: forwards;
  opacity: 0;
}

@keyframes fadeUpAnimeLeft {
  0% {
    opacity: 1;
    transform: translateY(90.625rem);
  }
  100% {
    opacity: 1;
  }
}
.loading-animation__right {
  animation-name: fadeUpAnimeRight;
  animation-duration: 3.5s;
  animation-fill-mode: forwards;
  opacity: 0;
}

@keyframes fadeUpAnimeRight {
  0% {
    opacity: 1;
    transform: translateY(90.625rem);
  }
  100% {
    opacity: 1;
  }
}
.loading-animation__delay {
  animation-delay: 0.5s;
}

