.sitemap__lower-fv {
    background-image: url(../../assets/images/common/terms_fv-sp.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    @include mq("md") {
        background-image: url(../../assets/images/common/terms_fv-pc.jpg);
    }
}

.sitemap__wrapper {
    margin-top: rem(12);
    width: 100%;
    @include mq("md") {
        margin-top: rem(150);
        margin-inline: auto;
    }
}